import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TraceService } from '../../../services/trace.service';
import { CoinScore } from '../../../models/coin-score';
import { LegacyTransactions } from '../../../models/legacy-transactions';
import { CaseService } from 'src/app/services/case.service';
import { AddressAddComponent } from '../../../components/address-add/address-add.component';
import { PopoverController } from '@ionic/angular';
import * as flag from 'country-code-emoji';
import * as countryCodeLookup from 'country-code-lookup';
import _get from 'lodash/get';
import _has from 'lodash/get';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-bch',
  templateUrl: './bch.page.html',
  styleUrls: ['./bch.page.scss'],
})
export class BchPage implements OnInit {
  hash: string;
  coinScore: CoinScore;
  transactions: LegacyTransactions;
  owner: object;
  walletId: string;
  availableCases: any;
  countryFlag: any;
  countryFullName: any;
  backButtonText = '';

  writableCaseId: string;
  writableCases: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    private traceService: TraceService,
    private caseService: CaseService,
    public popoverController: PopoverController,
    public toastController: ToastController
  ) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe(paramMap => {
      if (!paramMap.has('hash')) {
        return;
      }

      this.hash = paramMap.get('hash');
      this.fetchData(this.hash);
    });
  }

  async fetchData(hash: string) {
    return await Promise.all([
      this.fetchScore(hash),
      this.fetchTransactions(),
      this.fetchCases()
    ]);
  }

  async fetchScore(hash) {
    await this.traceService.fetchCoinScore(hash, 'bch').toPromise().then(res => {
      // console.log(res);
      // this.coinScore = res as CoinScore;
      this.coinScore = res;
    })
    .catch(err => {
      console.error(`Error: ${err}`);
    });
  }

  async fetchTransactions() {
    await this.traceService.fetchCoinLegacyTransactions(this.hash, 'bch').toPromise().then(res => {
      this.transactions = _get(res, 'addressTransactionTables[0].summary', undefined);
      this.owner = res.addressEntities[this.hash].owner;
      this.walletId = res.addressEntities[this.hash].walletId

      this.countryFlag = res.addressEntities[this.hash].owner.country && flag(res.addressEntities[this.hash].owner.country);
      this.countryFullName = res.addressEntities[this.hash].owner.country && countryCodeLookup.byInternet(res.addressEntities[this.hash].owner.country);

      const seen = new Set();
      const cases = _get(res, 'cases', undefined);

      this.availableCases = cases[this.hash].filter(item => {
        const duplicate = seen.has(item.caseId);
        seen.add(item.caseId);
        const shouldShow = (item.isOwner === true || item.isShared === true) && !duplicate;
        return shouldShow;
      });
    });
  }

  async fetchCases() {
    await this.caseService.fetchCases().toPromise().then(res => {
      this.writableCases = res.filter(item => {
        return item.writeEnabled === true;
      });
    })
  }

  async presentAddPopover(ev: any) {
    const popover = await this.popoverController.create({
      component: AddressAddComponent,
      componentProps: {
        address: this.hash,
        coinType: 'bch',
        writableCases: this.writableCases
      },
      event: ev,
      translucent: true,
    });

    popover.onDidDismiss().then(data => {
      if(_has(data, 'data') && data.data === true) {
        this.presentToast();
        this.fetchTransactions();
      }
    });

    return await popover.present();
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: `You successfully added this address.`,
      duration: 3000
    });

    toast.present();
  }
}
